@charset "UTF-8";
body.menu-open {
  overflow: hidden;
}

#menu__toggle {
  opacity: 0;
}

#menu__toggle:checked + .menu__btn > span {
  transform: rotate(45deg);
}

#menu__toggle:checked + .menu__btn > span::before {
  top: 0;
  transform: rotate(0deg);
}

#menu__toggle:checked + .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}

#menu__toggle:checked ~ .menu__overlay {
  display: block;
}

#menu__toggle:checked ~ .menu__box {
  right: 0;
}

.menu__btn {
  position: fixed;
  top: 30px;
  right: 20px;
  width: 30px;
  height: 26px;
  cursor: pointer;
  z-index: 1;
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: black;
  transition-duration: 0.25s;
}

.menu__btn > span::before {
  content: '';
  top: -12px;
}

.menu__btn > span::after {
  content: '';
  top: 12px;
}

.menu__overlay {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(34, 33, 33, 0.7);
  /* Колір темного бекграунда */
}

.menu__box {
  display: block;
  position: fixed;
  top: 0px;
  right: -100%;
  width: 70%;
  height: 100%;
  margin: 0;
  padding: 80px 0;
  list-style: none;
  background-color: #fff;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
  transition-duration: 0.25s;
}
.mobile-logo {
  width: 70px;
  height: 70px;
}
@media screen and (min-width: 768px) {
  .menu__box {
    width: 40%;
  }
}

.menu__item {
  display: block;
  padding: 12px 24px;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  color: black;
  transition-duration: 0.25s;
}

