/* Стилізація ползунка для WebKit (Chrome, Safari) */
.range-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px; /* ширина ползунка */
  height: 20px; /* висота ползунка */
  background-color: #173647; /* новий колір для ползунка */
  border-radius: 50%; /* зробити ползунок круглим */
  cursor: pointer;
  margin-top: -3px; /* коригування позиції ползунка */
}

/* Стилізація ползунка для Gecko (Firefox) */
.range-input::-moz-range-thumb {
  width: 20px; /* ширина ползунка */
  height: 20px; /* висота ползунка */
  background-color: #173647; /* новий колір для ползунка */
  border: 2px solid #fff; /* колір обведення ползунка */
  border-radius: 50%; /* зробити ползунок круглим */
  cursor: pointer;
}

